body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* NORMALIZE */
* {
  margin: 0;
  padding: 0;
}

.App {
  width: 100vw;
  overflow: hidden;
}

li {
  list-style: none;
}

a {
  color: #aaa;
  font-size: 0.8em;
  text-decoration: none;
}

/* THEME */
.App {
  color: #222;
}

.text,
.snippet,
.description {
  margin-top: 1em;
  color: #666;
}
/*
.title {
  margin-top: 2em;
  max-width: 250px;
} */

button {
  width: 130px;
  height: 33px;
  margin-top: 3em;
  border: 1px solid #000;
  background-color: #fff;
  font-size: 0.8em;
}

.heading {
  display: flex;
  align-items: flex-end;
  margin-left: 5vw;
}

.heading.small {
  margin-left: 0px;
}

.heading .title {
  width: 200px;
}

.media,
.picture,
.icon,
.image-container {
  background-color: #222;
}

.icon {
  border-radius: 100%;
}

.flex {
  display: flex;
}

/* SHAPES */
.line {
  width: 30px;
  margin-bottom: 0.33em;
  margin-right: 1em;
  border-bottom: 1px solid #000;
}

.back-line {
  width: 20px;
  margin-bottom: 0.33em;
  margin-right: 1em;
  border-bottom: 1px solid #000;
}

.small .line {
  width: 20px;
  margin-right: 0.8em;
}

/* MENU-ICON */
.icon-container {
  /* background-color: red; */
  padding: 15px;
}

.menu-icon {
  width: 16px;
  height: 10px;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
  display: flex;

  align-items: center;
}

.menu-icon span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #000;
  border-radius: 1px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.menu-icon span:nth-child(1) {
  top: 0px;
}

.menu-icon span:nth-child(2) {
  top: 5px;
}

.menu-icon span:nth-child(3) {
  top: 10px;
}

.menu-icon span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #000;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

#header {
  position: fixed;
  left: 0;
  top: 0;
  width: calc(100vw - 50px);
  display: flex;
  justify-content: space-between;
  padding: 40px 25px;
  align-items: center;
  z-index: 200;
}

#header .logo {
  padding: 3px;
}

#header .menu-list {
  display: none;
  align-items: center;
  height: 1em;
}

#header .navigation .menu-item {
  width: 80px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-start;
  margin: 5px;
}

#header .menu-list p {
  width: 60px;
  font-size: 0.9em;
  font-weight: 100;
  padding: 5px;
  color: #222;
}

#header .menu-item .line {
  width: 0px;
  margin-bottom: 0.45em;
  margin-right: 0em;
  border-bottom: 1px solid #222;
  -webkit-transition: width 0.3s ease;
  transition: width 0.3s ease;
}

#header .menu-item:hover .line {
  width: 10px;
}

#header .menu-item:active .line {
  width: 10px;
}

#header .menu-item.active .line {
  display: block;
}

@media only screen and (min-width: 768px) {
  #header {
    width: calc(100vw - 100px);
    padding: 60px 50px;
  }

  #header .menu-list {
    display: flex;
  }

  #header .navigation {
    width: 500px;
    justify-content: flex-end;
    align-items: center;
  }
}

